@use "sass:math";

// rem16px10px1610
@function rem($pixels) {
  @return math.div($pixels, 16) * 1rem;
}

@font-face {
  font-family: "bananaslip";
  src: url("../../fonts/YDWbananaslipplus.otf") format("opentype");
}

////////////////////
// 
////////////////////
// 
$inner: 1100px;

// full
$full: 1440;

// 
$padding-pc: 25px;
$padding-sp: rem(10);

////////////////////
// 
////////////////////

$base-font-family: "Noto Sans JP", sans-serif;
$second-font-family: "bananaslip", serif;

// 
$light: 300;
$normal: 400;
$medium: 500;
$bold: 700;

////////////////////
// 
////////////////////

$black: #000;
$white: #fff;
$red: #e63d48;
$green: #009443;
$beige: #f7efe3;
$brown: #c3925e;
$pink: #eb6ab7;

////////////////////
// 
////////////////////

//
$breakpoints: (
  sm: 500,
  sl: 600,
  md: 768,
  mm: 840,
  ml: 1024,
  xl: 1440,
  ll: 1600
);

//
$mediaquerys: (
  sm: "screen and (min-width: #{map-get($breakpoints,'sm')}px)",
  sl: "screen and (min-width: #{map-get($breakpoints,'sl')}px)",
  md: "screen and (min-width: #{map-get($breakpoints,'md')}px)",
  mm: "screen and (min-width: #{map-get($breakpoints,'mm')}px)",
  ml: "screen and (min-width: #{map-get($breakpoints,'ml')}px)",
  lg: "screen and (min-width: #{map-get($breakpoints,'lg')}px)",
  xl: "screen and (min-width: #{map-get($breakpoints,'xl')}px)",
  ll: "screen and (min-width: #{map-get($breakpoints,'ll')}px)"
);

// @include mq(){}md
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}

body {
  color: $black;
  background-color: $white;
  font-family: $base-font-family;
  font-weight: $normal;
  font-size: rem(16);
  font-style: normal;
}

body.modal-open {
  overflow: hidden;
}

html {
  @include mq("md") {
    scroll-padding-top: rem(40);
  }
}
